          margin-inline-start: 12px;
}

.pickles {
  overflow: hidden;

  line-height: 22px;
  color: #272727;
  -webkit-margin-start: 20px;
          margin-inline-start: 20px;
}

.meat {
  position: relative;
  z-index: 1;
  max-width: 45vw;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 20px;
  background: #fbfbfb7a;
}

.bread-buffer1 {
  position: absolute;
  top: -30px;
  left: -100px;
  width: 450px;
}

.bread-slice1 {
  position: relative;
  top: 0px;
  left: 0px;
  width: 450px;
  transform: rotate(20deg);
  transition: transform 1.2s ease-in-out;
}

.bread-buffer1:hover .bread-slice1 {
  transform: rotate(-16deg);
  position: relative;
  z-index: -1;
  transition: transform 1.2s ease-in-out;
}

.bread-slice2 {
  position: relative;
  bottom: 0px;
  right: 0px;
  width: 450px;
  transform: rotate(-20deg);
  transition: transform 1.2s ease-in-out;
}

.bread-buffer2 {
  position: absolute;
  bottom: -60px;
  right: -80px;
  width: 450px;
}

.bread-buffer2:hover .bread-slice2 {
  transform: rotate(16deg);
  position: relative;
  z-index: -1;
}

.doing-stuff {
  font-family: 'whyte';
  font-size: 16px;
}

.stuff {
  -webkit-margin-start: 20px;
          margin-inline-start: 20px;
  font-size: 16px;
  font-family: 'whyte-light';
